/* ==========================
Stili base (versione aggiornata)
========================== */
:root{
  --bg: #f2f2f2;
  --text: #333;
  --muted: #666;
  --accent: #007aff;
  --accent-600: #0058d6;
  --card: #fff;
  --border: #e6e6e6;
  --shadow: 0 8px 24px rgba(0,0,0,0.08);
  --radius: 12px;
  --safe-bottom: env(safe-area-inset-bottom, 12px);
}

* { box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--bg);
  color: var(--text);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -webkit-user-select: text;
  touch-action: manipulation;
}

/* Links */
a { text-decoration: none; color: var(--accent); }
a:hover { color: #0055ff; }

/* Headings & text */
h1,h2,h3,h4,h5,h6 { font-weight: 700; color: var(--text); margin:0 0 12px 0; }
p { color: var(--muted); margin:0 0 12px 0; }

/* Images */
img { width:100%; height:auto; border-radius:10px; display:block; }

/* Container */
.container {
  max-width: 1000px;
  margin: 40px auto;
  padding: 20px;
  background: var(--card);
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.06);
}

/* Header & nav */
header { background:#3498db; color:#fff; padding:20px; text-align:center; border-radius:10px 10px 0 0; }
header h1{ font-size:24px; margin:0; }
nav ul{ list-style:none; margin:12px 0 0 0; padding:0; display:flex; justify-content:space-between; gap:12px; }
nav li{ margin-right:20px; }
nav a{ color:#fff; text-decoration:none; }
nav a:hover{ color:#ccc; }

/* Articoli layout */
.articoli{ display:flex; flex-wrap:wrap; justify-content:space-between; gap:20px; margin-top:20px; }
.articolo{
  margin:0; padding:20px; background:#f7f7f7; border:1px solid #ddd; border-radius:10px;
  width: calc(50% - 10px); box-shadow: 0 2px 6px rgba(0,0,0,0.02);
}
.articolo h2{ margin-top:0; font-size:18px; }
.articolo p{ margin-bottom:0; }

/* ==========================
IA-CHAT: Box migliorato, altamente responsive
========================== */
/* Mobile-first base: occupa larghezza con margini, ma può comprimersi */
.ia-chat {
  position: fixed;
  left: 12px;
  right: 12px;
  bottom: calc(12px + var(--safe-bottom));
  margin: 0 auto;
  width: auto;
  max-width: 680px;            /* non troppo largo su tablet/desktop */
  min-width: 260px;            /* non troppo stretto su piccoli schermi */
  background: linear-gradient(180deg, rgba(255,255,255,0.98), var(--card));
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 12px;
  box-shadow: var(--shadow);
  z-index: 9999;
  display: grid;
  grid-template-columns: 1fr;   /* cambia in row su schermi più ampi */
  gap: 10px;
  transition: transform 220ms cubic-bezier(.2,.8,.2,1), opacity 180ms ease, visibility 180ms;
  transform-origin: center bottom;
  will-change: transform, opacity;
}

/* Icona/mini header interno per versione compatta */
.ia-chat .header-row {
  display:flex;
  align-items:center;
  gap:8px;
  justify-content:space-between;
}
.ia-chat .header-row h3{ margin:0; font-size:15px; color:var(--text); font-weight:600; }

/* Stato nascosto */
.ia-chat.hidden {
  transform: translateY(120%);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

/* Layout adattivo: quando c'è spazio, mostra colonne per pane risultati + input */
@media (min-width: 860px) {
  .ia-chat {
    grid-template-columns: 1fr 320px;  /* conversazione a sx, pannello di controllo / input a dx */
    padding: 14px;
    max-width: 880px;
  }
}

/* Chat window: dimensione fluid e scorrevole */
.ia-chat .chat-window {
  width: 100%;
  max-height: 48vh;            /* viewport-based per gestione tastiera */
  min-height: 90px;
  overflow-y: auto;
  padding: 8px;
  background: var(--card);
  border-radius: 10px;
  border: 1px solid #f0f0f0;
  -webkit-overflow-scrolling: touch;
}
@media (min-width:860px){
  .ia-chat .chat-window { max-height: 60vh; }
}
.ia-chat .chat-window div { margin-bottom:8px; line-height:1.35; }
.ia-chat .chat-window .msg-user { font-weight:600; display:inline-block; width:48px; color:var(--accent); }

/* Panel destro (su desktop/tablet): input e controlli */
.ia-chat .panel {
  display:flex;
  flex-direction:column;
  gap:8px;
  align-items:stretch;
}

/* Input row: responsive, con pulsante icona ridimensionabile */
.ia-chat .input-row {
  display:flex;
  gap:8px;
  align-items:center;
  width:100%;
}
.ia-chat input[type="text"]{
  flex:1;
  padding:10px 12px;
  font-size:15px;
  border-radius:8px;
  border:1px solid #d5d5d5;
  background: #fff;
}
.ia-chat .btn {
  display:inline-flex;
  align-items:center;
  justify-content:center;
  padding:10px 12px;
  border-radius:8px;
  border:none;
  background:var(--accent);
  color:#fff;
  cursor:pointer;
  font-weight:600;
  transition:transform 120ms ease, box-shadow 120ms ease, background 120ms ease;
}
.ia-chat .btn.secondary {
  background:#fff;
  color:var(--text);
  border:1px solid #ddd;
  box-shadow:none;
}
.ia-chat .btn:active{ transform: translateY(1px); }
.ia-chat .btn:focus{ outline:3px solid rgba(0,122,255,0.12); outline-offset:2px; border-radius:10px; }

/* Controls area: azioni rapide e counter */
.ia-chat .controls {
  display:flex;
  gap:8px;
  align-items:center;
  justify-content:flex-start;
  flex-wrap:wrap;
}
.ia-chat .controls .rescan-btn{
  padding:8px 10px;
  border-radius:8px;
  border:1px solid #e0e0e0;
  background:#fff;
  cursor:pointer;
  font-size:14px;
}
.ia-chat .controls .counter{
  margin-left:auto;
  color:var(--muted);
  font-size:13px;
}

/* Results / suggestions list: touch-friendly */
.ia-chat .results{ font-size:13px; margin-top:6px; }
.ia-chat .results ul{ margin:6px 0 0 18px; padding:0; }
.ia-chat .results li{
  margin-bottom:6px;
  padding:8px;
  border-radius:8px;
  background:linear-gradient(180deg, rgba(0,0,0,0.02), transparent);
  cursor:pointer;
  transition:background 120ms ease, transform 120ms ease;
}
.ia-chat .results li:hover{ background: rgba(0,0,0,0.04); transform: translateY(-1px); }

/* Compact mode: quando lo spazio verticale è ridotto (es. tastiera visible) */
.ia-chat.compact {
  padding:8px;
  gap:6px;
}
.ia-chat.compact .chat-window { max-height: 34vh; min-height:60px; padding:6px; }
.ia-chat.compact .input-row input{ padding:8px 10px; font-size:14px; }

/* ==========================
Pulsante toggle (apri/chiudi) migliorato
========================== */
.ia-toggle {
  position: fixed;
  right: calc(12px + var(--safe-bottom) / 6);
  bottom: calc(12px + var(--safe-bottom));
  z-index: 10000;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display:inline-flex;
  align-items:center;
  justify-content:center;
  background: linear-gradient(180deg, var(--accent), var(--accent-600));
  color: #fff;
  border: none;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
  cursor: pointer;
  transition: transform 160ms ease, opacity 140ms ease;
  font-size: 22px;
}
.ia-chat:not(.hidden) + .ia-toggle,
.ia-chat-visible + .ia-toggle{
  opacity: 0;
  pointer-events: none;
  transform: scale(0.85);
}
.ia-toggle:focus{ outline:3px solid rgba(52,152,219,0.18); outline-offset:2px; }

/* ==========================
Accessibilità & focus
========================== */
.ia-chat button:focus,
.ia-chat input:focus,
.ia-toggle:focus {
  outline: 3px solid rgba(52,152,219,0.14);
  outline-offset: 2px;
  border-radius: 10px;
}

/* ==========================
Transizioni e performance
========================== */
@media (prefers-reduced-motion: reduce){
  .ia-chat, .ia-toggle, .ia-chat .results li { transition: none; }
}

/* ==========================
Responsive fallback tweaks
========================== */
@media (max-width: 640px){
  nav ul{ flex-direction:column; align-items:flex-start; gap:8px; }
  .articolo{ width:100%; padding:16px; }
  header{ border-radius:0; }
  .ia-chat{ left:10px; right:10px; padding:10px; min-width:220px; }
  .ia-chat .chat-window{ max-height:44vh; }
}

/* Tablet and up: layout più spazioso */
@media (min-width: 641px) and (max-width: 859px){
  .articolo{ width: calc(50% - 10px); }
  .ia-chat{ max-width:720px; grid-template-columns: 1fr; padding:12px; }
  .ia-chat .chat-window{ max-height:52vh; }
}

/* Desktop: colonna + pannello destro */
@media (min-width: 860px){
  .ia-chat{ left:auto; right:16px; width:380px; max-width:420px; grid-template-columns:1fr; }
  /* If you prefer the two-column IA panel on large screens, uncomment below and adjust:
  .ia-chat{ grid-template-columns: 1fr 320px; width:auto; max-width:880px; }
  */
}

/* Utility: forzare nascondimento */
.chat-hidden .ia-chat { transform: translateY(120%); opacity:0; visibility:hidden; pointer-events:none; }
